<template>
  <div class="game__nav game__nav--enemy">
    <div
      class="game__charstate"
      v-for="(char, key) in foes"
      :key="key"
    >
        {{effectList(char.id)}}
      <EnemyCharFace
        :health='char.health'
        :energy='char.energy'
        :facepic='char.facepic'
        :index='key'
        :id='char.id'
      ></EnemyCharFace>
    </div>
  </div>

</template>

<script>
import EnemyCharFace from "@/components/_IngameEnemyCharFace.vue";

export default {
  components: {
    EnemyCharFace
  },
  computed: {
    foes() {
      return this.$store.getters["ingame/getMyFoes"];
    },

    effectList: function(){
      return this.$store.getters['ingame/getSkillQueueFilter'];
    }
  }
};
</script>