<template>
  <article class="game__charface">

    <div
      class="game__char"
      v-if="!targetStatus"
    >
      <img :src="`/img/game/${facepic}.jpg`">
    </div>

    <div
      v-else
      @click="sel(index)"
      v-on-click-outside='cancel'
      class="game__char game__char--target"
    >
      <img :src="`/img/game/${facepic}.jpg`">
    </div>

    <Bars
      :health='health'
      :energy='energy'
    />

  </article>
</template>


<script>
import Bars from "@/components/_GaugeBars.vue";
import targetHandle from "@/helper/targetHandle";

export default {
  components: {
    Bars
  },
  mixins:[targetHandle],
  props: ["facepic", "health", "energy"],
  computed: {
    targetStatus: function() {
      return this.$store.getters["ingame/getTargetEnemyStatus"];
    }
  },
  methods: {
    cancel: function() {
      this.$store.commit("ingame/SET_TARGET_ALLY_STATUS", false);
      this.$store.commit("ingame/SET_TARGET_ENEMY_STATUS", false);
      this.$store.commit("ingame/SET_SELECTED_SKILL", {});
    },
    sel() {
      const selectFunction = this.$store.getters['ingame/getSelectedSkill'].skill.target
      const targets = this[selectFunction]()

      const payload = {
        skill: this.$store.getters["ingame/getSelectedSkill"].skill,
        skillIndex: this.$store.getters["ingame/getSelectedSkill"].skillIndex,
        targets: targets,
        caster: this.$store.getters["ingame/getSelectedSkill"].caster,
        position: this.$store.getters["ingame/getSelectedSkill"].position
      };
      this.$store.commit("ingame/ADD_TO_SKILL_QUEUE", payload);
      this.$store.commit("ingame/SET_SELECTED_SKILL", {});
    }
  }
};
</script>