export default {
    props: ["index", "id"],
    methods: {
        oneEnemy: function () {
            return [
                {
                    target: this.id,
                    position: this.index
                }
            ]
        },
        allEnemies: function () {
            let targets = [
                {
                    target: this.id,
                    position: this.index
                }
            ];
            const foes = this.$store.getters["ingame/getMyFoes"];
            for (const i in foes) {
                if (foes[i].id !== this.id)
                    targets.push({
                        target: foes[i].id,
                        position: i
                    });
            }
            return targets
        }
    }
}