<template>
  <article class="game__charface">
    
    <div
      class="game__char"
      v-if="!targetStatus"
    >
      <img :src="`/img/game/${facepic}.jpg`">
    </div>

    <div
      v-else
      @click="sel"
      v-on-click-outside='cancel'
      class="game__char game__char--target"
    >
      <img :src="`/img/game/${facepic}.jpg`">
    </div>
    <div class="game__bar">
      <p
        class="game__bar__value"
        :style='{width:health+"%"}'
      ></p>
      <p class="game__bar__load">{{ health }} hp</p>
    </div>
    <div class="game__bar">
      <p
        class="game__bar__value game__bar__value--sp"
        :style='{width:energy+"%"}'
      ></p>
      <p class="game__bar__load">{{ energy }} sp</p>
    </div>
  </article>
</template>


<script>
export default {
  props: ["facepic", "health", "energy"],
  computed: {
    targetStatus: function() {
      return this.$store.getters['ingame/getTargetAllyStatus'];
    }
  },
  methods: {
    cancel: function() {
      this.$store.commit("ingame/SET_TARGET_ALLY_STATUS", false);
      this.$store.commit("ingame/SET_TARGET_ENEMY_STATUS", false);
    },
    sel() {
      this.$store.getters['ingame/getSelectedSkill']
    }
  }
};
</script>