<template>
  <div class="game">

    <div>
      <h1>{{timer}}</h1>
      <button
        v-if="myturn"
        @click="endTurn"
      >End turn</button>
    </div>

    <div class="game__nav game__nav--ingame">
      <div
        class="game__charstate"
        v-for="(char, key) in friends"
        :key="key"
      >
        <CharFace
          :health='char.health'
          :energy='char.energy'
          :facepic='char.facepic'
        ></CharFace>

        <div class="game__skillset">
          <div
            class="game__skillset__pic"
            v-for="(skill, k) in char.skills"
            :key="k"
            @click="selectSkill(skill, char.id, key, k)"
          >
            <img :src="`/img/game/${skill.skillpic}.jpg`">
          </div>
        </div>
      </div>
    </div>

    <Enemies></Enemies>
  </div>
</template>

<script>
import CharFace from "@/components/_IngameCharFace.vue";
import Enemies from "@/components/_Enemies.vue"
export default {
  components: {
    CharFace,
    Enemies
  },
  data() {
    return {
      timer: 60,
      interval: false,
      offensiveTarget:["oneEnemy", "allEnemies"]
    };
  },
  sockets: {
    startTurn: function(payload) {
      this.endTimer();
      this.$store.commit("ingame/SET_GAME", payload);
      this.timer = 60;
      this.startTimer();
    },
    endTurn: function(payload) {
      this.endTimer();
      this.$store.commit("ingame/SET_TARGET_ENEMY_STATUS", false);
      this.$store.commit("ingame/RESET_SKILL_QUEUE")
      this.$store.commit("ingame/SET_GAME", payload);
      this.timer = 60;
      this.startTimer();
    },
    forcedEndTurn: function() {
      this.$socket.client.emit("endTurn", []);
    }
  },
  computed: {
    opponent() {
      return this.$store.getters["ingame/getMyOpponent"];
    },
    myturn() {
      return this.$store.getters["ingame/getMyTurn"];
    },
    friends() {
      return this.$store.getters["ingame/getMyFriends"];
    },
  },
  methods: {
    endTurn() {
      this.$socket.client.emit("endTurn", this.$store.getters['ingame/getSkillQueue']);
    },
    startTimer() {
      this.interval = setInterval(
        function(that) {
          that.timer--;
        },
        1000,
        this
      );
    },
    endTimer() {
      clearInterval(this.interval);
    },
    selectSkill(skill, user, position, skillIndex) {
      if (this.myturn && this.offensiveTarget.includes(skill.target)) {
        this.$store.commit("ingame/SET_SELECTED_SKILL", {
          caster: user,
          position: position,
          skill,
          skillIndex: skillIndex
        })
        this.$store.commit("ingame/SET_TARGET_ENEMY_STATUS", true);
      }
    }
  },
  created() {
    this.startTimer();
  }
};
</script>